import PlayerTile from './PlayerTile.js';
import React from 'react';
import cloneDeep from 'lodash/cloneDeep';
import 'purecss/build/pure.css';
import './App.css'

function getStartState () {
    const startState = {
        rename: true,
        past: {
            past: 0,
            state: 0
        },
        present: {
            players: [
                {"id": "1", "name": "", "status": "active"},
                {"id": "2", "name": "", "status": "active"},
                {"id": "3", "name": "", "status": "active"},
                {"id": "4", "name": "", "status": "active"},
                {"id": "5", "name": "", "status": "active"},
                {"id": "6", "name": "", "status": "active"},
                {"id": "7", "name": "", "status": "active"}
            ],
            matchHistory: []
        }
    };
    return cloneDeep(startState);
}


class App extends React.Component {
  constructor(props) {
    super(props)
    this.handleMatch = this.handleMatch.bind(this);
    this.handleEliminate = this.handleEliminate.bind(this);
    this.handleUndo = this.handleUndo.bind(this);
    this.handleText = this.handleText.bind(this);
    this.handleReset = this.handleReset.bind(this);
    this.handleRename = this.handleRename.bind(this);
    this.state = getStartState();
  }

  handleText(e) {
    const players = this.state.present.players
    players[e.target.id - 1].name = e.target.value
    this.setState({
        players: players
    });
  }
  handleRename(e) {
    const rename_toggle = !this.state.rename
    this.setState({
        rename: rename_toggle
    })
  }
  handleReset(e) {
    const s = getStartState();

    this.setState({
        past: {
            past: s.past.past,
            state: s.past.state
        },
        present: {
            players: s.players,
            matchHistory: s.matchHistory
        }
    });
    console.log(this.state);
  }

  handleMatch(e) {
    const players = this.state.present.players
    const snapshot_past = cloneDeep(this.state.past)
    const snapshot_present = cloneDeep(this.state.present)
    players[e.target.id - 1].status="matched"
    this.setState({
        past: {
            past: snapshot_past,
            state: snapshot_present
        },
        players: players
    });
  }

  handleEliminate(e) {
    const players = this.state.present.players
    const snapshot_past = cloneDeep(this.state.past)
    const snapshot_present = cloneDeep(this.state.present)
    players[e.target.id - 1].status="eliminated"
    players.forEach((p) => {
        if (p.status == "matched") {
            p.status = "active";
        }
    })

    this.setState({
        past: {
            past: snapshot_past,
            state: snapshot_present
        },
        players: players
    });
  }

  handleUndo(e) {
    const past = cloneDeep(this.state.past)
    const pastpast = cloneDeep(this.state.past.past)
    console.log(past)
    this.setState({
        past: pastpast,
        present: {
            players: past.state.players,
            matchHistory: past.state.matchHistory
        }
    })
  }

  render() {
    const ActiveList = this.state.present.players.map((p) => {
          if (p.status != "eliminated") {
              return <PlayerTile 
                id={p.id} 
                status={p.status} 
                handleMatch={this.handleMatch} 
                handleEliminate={this.handleEliminate}
                handleText={this.handleText}
                name={p.name}
              />
          }
      }
    );
    const EliminatedList = this.state.present.players.map((p) => {
          if (p.status === "eliminated") {
              return <PlayerTile 
                id={p.id} 
                status={p.status} 
                handleMatch={this.handleMatch} 
                handleEliminate={this.handleEliminate}
                handleText={this.handleText}
                name={p.name}
              />
          }
        }
    );  

    const rename_active = this.state.rename ? "pure-button-active":""
    return (
      <div className='app'> 
            <div className='header'>
            <h1>TFT SCOUTER</h1>
            <p>Press TAB to quickly input player names</p> 
            <p>🔴 = possible to face them next round</p>
            </div>
            {ActiveList}
            {EliminatedList}
        <div class="pure-button-group" role="group" aria-label="...">
            <button 
                className="undo-button pure-button"
                onClick={this.handleUndo} 
                tabindex="-1"
                disabled={this.state.past.past === 0}
            >Undo</button>
            <button
                className={"rename-button pure-button " + rename_active}
                onClick={this.handleRename}
                tabindex="-1"
            >Rename</button>
            <button 
                className="reset-button pure-button"
                onClick={this.handleReset}
                tabindex="-1"
            >Reset</button>
        </div>
      </div>
    );
  }
}

export default App;
