import React from 'react';

class PlayerTile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {value: ''};
  }

  render() {
    var color = "";
    switch (this.props.status) {
        case "active":
            color = "red";
            break;
        case "matched":
            color = "green";
            break;
        case "eliminated":
            color = "grey";
            break;
    }

    return (
        <div className={"PlayerTile pure-g " + color}>
            <div className='pure-u-14-24'>
                <input 
                    className={color}
                    id={this.props.id} 
                    type="text" 
                    onChange={this.props.handleText}
                    autocomplete="off" 
                    value={this.props.name}
                    onClick={this.props.handleMatch}
                    disabled={this.props.renameMode}
                />
            </div>
            <div className='pure-u-10-24'>
                <div className='pure-button-group' role='group' aria-label='...'>
                <button 
                    className="pure-button match-button"
                    id={this.props.id} 
                    type="button" 
                    onClick={this.props.handleMatch} 
                    tabindex="-1"
                    disabled={this.props.status === "eliminated"}
                >
                    Match
                </button>
                <button 
                    className="pure-button eliminate-button"
                    id={this.props.id}
                    onClick={this.props.handleEliminate}
                    tabindex="-1"
                    disabled={this.props.status === "eliminated"}
                >
                    Eliminate
                </button>
                </div>
            </div>
        </div>
    );
  }
}

export default PlayerTile;
